form {
  padding: 20px 40px 0;
}

form ul {
  list-style-type: none;
}

form ul li {
  padding-bottom: 10px;
}

form ul li label {
  display: block;
  float: left;
  width: 80px;
  text-align: right;
  padding-right: 10px;
  font-size: 13px;
  line-height: 28px;
}

form ul li label::after {
  content: ':';
}

form ul li input,
form ul li textarea {
  display: block;
  padding-left: 10px;
  padding-right: 28px;
  width: calc(100% - 80px);
  border: 1px solid #cccccc;
  background-color: #fff;
  background-position: right 6px top 6px;
  background-repeat: no-repeat;
  transition: background-color .15s;
}

