form ul li textarea[required]:valid {
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 14 14"><path fill="#37ca41" fill-rule="evenodd" d="M675,766 C671.134007,766 668,762.865993 668,759 C668,755.134007 671.134007,752 675,752 C678.865993,752 682,755.134007 682,759 C682,762.865993 678.865993,766 675,766 Z M674.035534,762.571068 L679.692388,756.914214 L678.278175,755.5 L674.035534,759.742641 L671.914214,757.62132 L670.5,759.035534 L674.035534,762.571068 Z" transform="translate(-668 -752)"/></svg>');
